h1,h2,h3,h4{margin:5px 0px}h2,h3,h4,p,a{font-family:'Roboto Condensed', sans-serif}h1{color:#b8f4ff;font-size:30px;font-weight:bold;font-style:italic;font-family:'Orbitron', sans-serif;-webkit-text-stroke-width:1px;-webkit-text-stroke-color:#000;text-shadow:3px 3px 5px #006e00}h2{color:#fff;font-size:26px;text-shadow:2px 2px 4px #460031}h3{color:#fff;font-size:22px;text-shadow:2px 2px 5px #990606}h4{color:#fff;font-size:16px;text-shadow:4px 4px 4px #060042}p{color:#fff;font-size:18px;text-shadow:4px 4px 4px #060042}.unit{position:absolute;display:flex;top:20px;width:100px;height:100px;z-index:1}.player-attack{position:absolute;display:flex;top:20px;left:20px;width:90px;height:90px;z-index:2;-webkit-filter:drop-shadow(0px 0px 10px blue);filter:drop-shadow(0px 0px 10px blue)}.enemy-attack{position:absolute;display:flex;top:20px;left:20px;width:90px;height:90px;z-index:2;-webkit-filter:drop-shadow(0px 0px 10px red);filter:drop-shadow(0px 0px 10px red)}html{background:radial-gradient(circle, #ff0303 0%, #000 100%);background-color:#32004e;background-image:url(images/back2.jpg);background-blend-mode:difference;background-size:cover}body{margin:0px;width:100%;height:100%}.player{-webkit-filter:hue-rotate(45deg) drop-shadow(0px 0px 10px blue);filter:hue-rotate(45deg) drop-shadow(0px 0px 10px blue)}.enemy{-webkit-filter:hue-rotate(270deg) drop-shadow(0px 10px 10px red);filter:hue-rotate(270deg) drop-shadow(0px 10px 10px red);width:100px;height:100px}.health-bar{-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none;opacity:85%;position:absolute;display:flex;color:#fff;z-index:2;-webkit-text-stroke-width:1px;-webkit-text-stroke-color:#000;font-family:Arial, Helvetica, sans-serif;-webkit-filter:drop-shadow(0px 0px 10px red);filter:drop-shadow(0px 0px 10px red)}#target{position:absolute;display:flex;top:450px;left:450px;color:red;font-family:fantasy;font-size:40px}.board-square-player,.board-square-enemy{display:flex;position:absolute;width:100px;height:100px}.board-square-player{box-shadow:rgba(0,0,255,0.9) 0px 0px 15px;background-color:rgba(255,255,255,0.15)}.board-square-enemy{box-shadow:rgba(255,0,0,0.9) 0px 0px 15px;background-color:rgba(0,0,0,0.2)}.main{width:100%;height:100%;display:flex;flex-direction:column;justify-content:center;align-items:center}.main-top{width:800px;display:flex;justify-content:center;background-size:contain;box-shadow:0px 5px 10px #f60;border-radius:20px;background-color:rgba(255,0,0,0.192)}.main-top h1{margin:0px;opacity:100%;border-radius:10px;color:#79efff;font-size:45px;font-weight:bold;font-style:italic;-webkit-text-stroke-width:2px;-webkit-text-stroke-color:black;text-shadow:5px 5px 10px blue}.main-middle{display:flex;flex-direction:row;justify-content:center;margin:20px;min-width:1500px}.main-bottom{height:40px;width:800px;display:flex;justify-content:space-around;align-items:center;background-size:contain;box-shadow:0px 5px 10px #f60;border-radius:20px;background-color:rgba(41,0,0,0.534)}.units-sidebar{box-sizing:border-box;padding:10px;min-width:300px;width:300px;height:800px;border:rgba(255,255,255,0.52) 1px solid;justify-content:space-between;align-items:center;display:flex;flex-direction:column;border-radius:10px;background:linear-gradient(90deg, rgba(95,0,0,0.5) 0%, rgba(139,0,139,0.6) 33%, rgba(74,1,133,0.7) 66%, rgba(2,158,163,0.8) 100%);box-shadow:salmon -10px 10px 20px}.how-to-play{box-sizing:border-box;padding:20px;display:flex;align-items:center;flex-direction:column;justify-content:space-between;min-width:300px;width:300px;height:800px;border:black 2px solid;border-radius:10px;background:linear-gradient(90deg, rgba(2,158,163,0.8) 0%, rgba(74,1,133,0.7) 33%, rgba(139,0,139,0.6) 66%, rgba(95,0,0,0.5) 100%);box-shadow:salmon 10px 10px 20px}.how-to-play p{margin:0px}.how-to-play div{width:100%;display:flex;justify-content:space-between}.how-to-play button{margin:2px 0px}#speed{width:80%}.board{min-width:800px;position:relative;width:800px;height:800px;border:black 2px solid;margin:0px 20px;background-color:rgba(2,164,214,0.5);background-image:url(images/back4.jpg);background-size:100%;border-radius:10px;background-blend-mode:difference;-webkit-filter:drop-shadow(0px 0px 30px #0cf);filter:drop-shadow(0px 0px 30px #0cf)}#overlay{background-color:white;display:flex;flex-direction:column;justify-content:space-around;align-items:center;position:absolute;width:500px;height:250px;z-index:10;left:0;right:0;top:0;bottom:0;margin-left:auto;margin-right:auto;margin-top:auto;margin-bottom:auto;border-radius:30px;background:linear-gradient(0deg, rgba(167,0,0,0.7) 0%, rgba(9,0,193,0.7) 49%, rgba(45,220,253,0.7) 100%);box-shadow:10px 0px 10px #f60}#overlayBack{z-index:5;background-color:rgba(40,0,56,0.6);position:absolute;display:flex;top:0%;height:100%;width:100%}.add{padding:0px 10px;cursor:pointer;width:80%;display:flex;flex-direction:row;justify-content:space-between;align-items:center}.add:hover{background-color:rgba(0,0,255,0.274);border-radius:10px}.add-image{height:50px;width:50px;-webkit-filter:drop-shadow(0px 0px 8px #fff);filter:drop-shadow(0px 0px 8px #fff)}button{cursor:pointer;border:rebeccapurple 3px solid;color:#002499;background-color:rgba(255,244,231,0.8);text-shadow:#ff9e78 0px 2px 2px;border-radius:20px;font-size:16px;font-weight:bold;font-style:italic;margin:5px;padding:8px 0px;width:125px;box-shadow:-5px 5px 10px rgba(79,80,172,0.7)}#start-fight-button{font-size:24px;padding:10px;width:200px}button:hover{background-color:rgba(3,84,190,0.8);color:#f1daff;text-shadow:#46b0ca 0px 2px 2px}.button-on{background-color:rgba(3,84,190,0.8);color:#f1daff;text-shadow:#46b0ca 0px 2px 2px}.button-on:hover{background-color:rgba(0,49,114,0.8);color:#bd77ff;text-shadow:#ff41e6 0px 2px 2px}#stats{padding:10px;width:80%;height:200px;background-color:rgba(0,0,37,0.459);border-radius:20px}.main-bottom div{width:250px;display:flex;height:35px;align-items:center;justify-content:space-around}.main-bottom h1{font-size:20px;text-shadow:2px 